  width: 64px;
  height: 64px;
  border-radius: 50%;
}

.project_title {
  display: flex;
  align: center;
  align-items: center;
}

.project_name {
  margin: 0px 20px;
  font-size: xx-large;
}

.project_description {
  color: gray;
  font-size: large;
  font-weight: normal;
  margin: 20px 5vw 0px 0px;
}

.techstacks {
  display: grid;
  grid-template-columns: auto auto;
}

.techstack {
  color: gray;
  font-size: large;
  font-weight: normal;
}

.heading {
  font-size: 6vw;
  text-align: center;
  margin-bottom: -20px;
}

@media screen and (max-width: 800px) {
  .heading {
    font-size: 8vw;
  }
}

.subheading {
  margin-bottom: 8px;
}

.review {
  color: gray;
  margin-right: 5vw;
  font-style: italic;
  font-weight: normal;
}

.button {
  color: black;
  font-size: 16px;
  cursor: pointer;
  margin-top: 20px;
  font-weight: bold;
  padding: 8px 16px;
  border-radius: 20px;
  border: 2px solid gray;
  background-color: white;
}

// Styling for Small Screen sizes
@media screen and (max-width: 800px) {
  .top_ui {
    z-index: 0;
    width: 50vw;
    height: 50vh;
  }

  .user_details {
    z-index: 2;
    width: 100vw;
    color: black;
    opacity: 0.9;
    height: 100vh;
    display: flex;
    background: white;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }

  .user_detail_image {
    z-index: 2;
    width: 25vh;
    height: 25vh;
    color: white;
    align: center;
    object-fit: cover;
    justify-content: center;
  }
}

